/* Add some basic styling */
input {
    width: 300px;
    padding: 10px;
    margin: 10px 0;
}

div {
    margin-left: 10px;
}
.response-textarea {
    width: 30%;
    height: 15%;
}
.search-bar {
    display: block;
    margin-left: auto;
    margin-right: auto;
    left: 10%;
    width: 50%;
}
.container {
    position: relative;
    text-align: center; /* Center-align items horizontally */
}
.container-header {
    font-size: 18px; /* Sets the font size */
    color: #4A4A4A; /* Sets the text color */
    font-weight: bold; /* Ensures the text is bold, which <b> usually does by default */
    padding: 5px 10px; /* Adds some padding around the text */
    background-color: #EFEFEF; /* Sets a background color for the header */
    border-bottom: 1px solid #D3D3D3; /* Adds a bottom border for a distinct look */
    display: block; /* Makes the <b> tag behave like a block-level element */
    margin-bottom: 10px; /* Adds some space below the header */
}
.group-section {
    margin-bottom: 20px;
}

.group-header {
    font-weight: bold;
    margin-bottom: 10px;
}

table {
    width: 100%;
    border-collapse: collapse;
}

td {
    padding: 5px;
    border: 1px solid #ccc;
}

.dropdown-menu {
    top: 100%; /* Adjust if you need some space between the search bar and dropdown */
    /*transform: translateX(-50%);*/
    transform: translateX(calc(-30% ));
    border: 1px solid #a19d9d;
    padding: 10px;
    position: absolute;
    background-color: white;
    width: 40%;
    height: 70vh;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
    overflow: auto;
    left: 10%;
}

.group-header {
    font-weight: bold;
    margin-top: 10px;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    margin: 5px 0;
}

.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
}

.popup-inner {
    background: white;
    padding: 20px;
    border-radius: 5px;
    position: relative; /* This ensures that the close button is positioned relative to this container */
    width: 70vw; /* Adjust the width as necessary */
    height: 80vh; /* Adjust the height as necessary */
    max-width: 800px; /* Maximum width */
    max-height: 600px; /* Maximum height */
    overflow: hidden; /* Prevent scrolling within the popup-inner */
}
.popup-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f1f1f1;
    padding: 10px;
    border-bottom: 1px solid #ccc;
}
.popup-title {
    margin: 0;
    font-weight: bold;
}

.popup-close-btn {
    position: absolute;
    top: 10px; /* Adjust as needed */
    right: 10px; /* Adjust as needed */
    cursor: pointer;
}
.popup-content {
    text-align: left !important;
    overflow-y: auto; /* Enable vertical scrolling within the content area */
    height: calc(100% - 50px); /* Adjust height to account for the header */
}